.userInfoPopUpHeading {
  text-align: center;
}
.userInfoFromMainContainer {
  padding-left: 30px;
  padding-right: 5px;
}
.userInfoLabelText {
  display: inline-block;
  font-size: 20px;
  width: 150px;
}
@media screen and (max-width: 768px) {
  .userInfoLabelText {
    width: 200px;
    margin-bottom: 3px;
  }
}
.userInfoInputField {
  width: 250px;
  height: 20px;
  border: 1px solid black;
  border-radius: 10px;
  padding: 5px;
}
.userInfoInputLabContainer {
  margin-bottom: 10px;
}
.popup {
  display: none;
  position: absolute;
  background-color: #333;
  color: #fff;
  padding: 10px;
  border-radius: 5px;
  z-index: 1;
  bottom: 160px;
  right: 110px;
}
@media screen and (max-width: 768px) {
  .popup {
    bottom: -150px;
    right: 95px;
  }
}

#sendButton:hover + #popup {
  display: block;
}
.recordingIndicator {
  font-size: 18px;
  font-weight: bold;
  color: red;
}

.blink {
  animation: blink-animation 1s infinite;
}

@keyframes blink-animation {
  0%,
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.btnTextContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.appTitle {
  font-family: "Bree serif";
  font-size: 25px;
  margin-left: 30px;
}

.questions {
  overflow-y: scroll;
  flex-grow: 1;
  margin-top: 2px;
  margin-bottom: 0px;
}

.MobileCommandsContainer {
  border: 1px solid #b2beb5;
  padding: 13px;
  width: 95%;
  margin-top: 0px;
}
@media screen and (min-width: 768px) {
  .MobileCommandsContainer {
    display: none !important;
  }
}
